const slot_exe_once = () =>{
	// KAKUHENtrueSLOT3RARITY0
	const SLOT3_KAKUHEN = KAKUHEN ? decrease_any_rarity(0, DECREASE_PERCENT) : SLOT3;
	// SLOT3_KAKUHEN1
	const random_SLOT3_KAKUHEN = shuffle(SLOT3_KAKUHEN)[0];
	// console.log(random_SLOT3_KAKUHEN);
	// GOLDGOLDGOLD
