		return;
	}else{
		GOLD -= 1;
	}
	// MINE
	// MINE.push(random_SLOT3_KAKUHEN);

	// ramda.jsMINE{RARITY: 1, LFP_BUFF: 0, LFP_DEBUFF: 0, ATK_BUFF: 2, ATK_DEBUFF: 0}
