		).every(V=>V.RARITY >= 1)) {
		KAKUHEN = true;
	}
	// KAKUHENRARITY0(50%RARITY0SLOT39045)10KAKUHENfalse
	if (KAKUHEN === true) {
		KAKUHEN_COUNTER += 1;
		KAKUHEN = KAKUHEN_COUNTER >= KAKUHEN_COUNTER_LIMIT ? false : true;
		KAKUHEN_COUNTER = KAKUHEN_COUNTER >= KAKUHEN_COUNTER_LIMIT ? 0 : KAKUHEN_COUNTER;
		// if (MINE.length >= 10) {
		// 	KAKUHEN = false;
		// }
	}
};



